.hobbies-page {
    position: relative;
    background-image: url("../../Pics/5.jpg");
    background-size: cover;
    z-index: 1;
}

.hobbies-page::before {
    content: ''; /* This is necessary for the pseudo-element to show */
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.8); /* This white overlay has 50% opacity */
    z-index: 1; /* Places the overlay above the background image */
  }
  
  .hobbies-page * {
    position: relative;
    z-index: 1;
  }

#hobby-boxes {
    display: flex;
    flex-wrap: wrap;
    gap: 4em;
    margin-top: 15px;
    justify-content: center !important;
    height: 400px;
  }
  
  .hobby-lists {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 1em;
    gap: 3em;
  }
  
  .hobby-box {
    position: relative;
    width: 198px;
    height: 354px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(240, 240, 240, 0.8);
    padding: 10px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.9);
    overflow: hidden; /* Changed from 'visible' to 'hidden' */
    margin-bottom: 5px;
  }
  
  .hobby-image {
    width: 180px;
    height: 180px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .hobby-image img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain; /* Changed from 'cover' to 'contain' */
  }
  
  .Learn-More-Button {
    width: calc(100% - 20px);
    padding: 10px;
    margin: 10px;
    background-color: rgb(161, 101, 144);
    border: 2px solid #000;
    border-radius: 15px;
    color: white;
    cursor: pointer;
    font-size: 1em;
    padding: 8px 16px;
    margin-top: 2px; /* Move the button to the bottom */
    transition: background 0.3s ease;
  }
  
  .Learn-More-Button:hover {
    background-color: 68, 4, 50;
  }

  .hobby-title {
    font-weight: bold;;
  }